import { Component, Prop, Event, EventEmitter, h, Host } from '@stencil/core';

@Component({
  tag: 'bh-page-navigation-single-level',
  styleUrl: 'bh-page-navigation-single-level.css',
  shadow: true,
})
export class BhPageNavigationSingleLevel {
  /**
   * Label text for the navigation item
   */
  @Prop() label!: string;

  /**
   * Material icon name
   */
  @Prop() icon: string = 'radio_button_unchecked';

  /**
   * Whether the item is currently active/selected
   */
  @Prop() isActive: boolean = false;

  /**
   * Event emitted when the navigation item is clicked
   */
  @Event() bhItemClick!: EventEmitter<void>;

  private handleClick = () => {
    this.bhItemClick.emit();
  };

  private getItemClasses() {
    return {
      'page-navigation-single-level': true,
      'page-navigation-animate-smooth': true,
      'active': this.isActive,
    };
  }

  render() {
    return (
      <Host>
        <div class={this.getItemClasses()} onClick={() => this.handleClick()}>
          <div class="page-navigation-item-content">
            <span class="page-navigation-item-icon material-symbols-outlined">
              {this.icon}
            </span>
            <span class="page-navigation-item-label">{this.label}</span>
          </div>
        </div>
      </Host>
    );
  }
}
